<template>
    <div id="details-product" v-if="product.name != null">
        <b-container>
            <b-row>
                <b-col class="text-left">
                    <b-button @click="goBack" class="btn-outline-primary">
                        <b-icon-chevron-left></b-icon-chevron-left>
                        Revenir aux produits
                    </b-button>
                </b-col>
            </b-row>
            <b-row>
                <b-col cols="12" md="6">
                    <div class="preview-container">
                        <div class="preview">
                            <b-img :src="selectedPreview.url" :alt="selectedPreview.alt"></b-img>
                        </div>
                        <div class="miniature-container">
                            <div class="miniature" v-for="pic of product.pictures"
                                 v-on:click="selectPreview(pic.picture)"
                                 v-bind:class="{'selected':(selectedPreview === pic.picture)}">
                                <b-img :src="pic.picture.url" :alt="pic.picture.alt"></b-img>
                            </div>
                        </div>
                    </div>
                </b-col>
                <b-col cols="12" md="6">
                    <h2 class="text-left">{{product.name}}</h2>
                    <h6>{{product.short_description}}</h6>
                    <p class="price">{{product.price}} €</p>
                    <p class="font-weight-bold">Description :</p>
                    <p>{{product.description}}</p>
                </b-col>
            </b-row>

            <b-row>
                <b-col>
                    <h3 class="text-left">Caractéristiques</h3>
                    <b-table :items="product.features" thead-class="hidden_header"></b-table>
                </b-col>
            </b-row>

            <product-selection title="Vous aimerez aussi"></product-selection>

        </b-container>
    </div>
</template>

<script>
    import Product from "../components/Product";
    import ProductSelection from "../components/ProductSelection";

    export default {
        name: 'DetailsProduct',
        components: {
            Product,
            ProductSelection
        },
        props: ['id'],
        data() {
            return {
                selectedPreview: null,
                product: {
                    name: null,
                    price: null,
                    short_description: null,
                    description: null,
                    pictures: [],
                    features: []
                }
            }
        },
        methods: {
            getProduct() {
                this.$prismic.client.getByUID('product', this.$route.params.id).then((res) => {
                    this.product = res.data;
                    this.selectedPreview = res.data['pictures'][0].picture;
                })
            },
            selectPreview(preview) {
                this.selectedPreview = preview;
            },
            goBack() {
                this.$router.go(-1);
            }
        },
        created() {
            this.getProduct();
        }
    }
</script>
